<template>
	<view>
		<image class="bg-img" src="/static/picture/bg_img.jpg"></image>
				<button type="primary" class="button" @click="getUserInfo">微信登录</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {

			}
		},
		mounted() {

		},

		methods: {
			getUserInfo() {
				const _this = this
				uni.getUserProfile({
					desc: '用于完善会员资料',
					success: (result) => {
						_this.userInfo = result.userInfo
						_this.wxLogin()
						uni.switchTab({
							url: '/pages/user/user'
						});
					},
					fail: () => {
						uni.hideLoading();
						uni.showModal({
							content: '获取用户信息失败',
							showCancel: false
						})
					}
				})
			},

			wxLogin() {
				const _this = this
				uni.showLoading({
					title: '加载中'
				});

				uni.login({
					provider: 'weixin',
					success: (res) => { // 获取 code
						if (res.code) {
							uniCloud.callFunction({
								name: 'login_cs',
								data: {
									action: 'code2Session',
									js_code: res.code,
									user_info: _this.userInfo
								},
								success: (res) => {
									console.log('云函数返回的值：：：：', res.result)
									uni.hideLoading();
									if (res.result.result.result._id) {
										uni.setStorageSync('userInfo', JSON.stringify(res.result
											.result.result))
										res.userInfo = res.result.result.result
									}
								},
								fail: () => {
									uni.hideLoading();
									console.log('云函数调用失败')
								}
							})
						}
					}
				})
			}


		}
	}
</script>

<style scoped lang="scss">
	.bg-img {
		position: fixed;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: -1;
	}

	.button {
		margin-top: 98%;
		margin-left: 30px;
		margin-right: 30px;
	}
</style>
